/* Header */
header {
    background-image: url('../images/bg.jpg');
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    padding: 100px 30px;
    position: relative;
    @media #{$mediaExSmall} {
        padding: 100px 70px;
    }
    @media #{$mediaSmall} {
        padding: 100px 65px;
    }
    @media #{$mediaMedium} {
        padding: 100px 60px;
    }
    @media #{$mediaLarge} {
        padding: 100px 50px;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(#000, .8);
        width: 100%;
        height: 100%;
        z-index: 9;
    }
    .profile {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 99;
        .img-profile {
            overflow: hidden;
            position: relative;
            width: 60%;
            height: 400px;
            margin: auto;
            border: 5px solid #fff;
            border-radius: 2px;
            box-shadow: 0px 0px 10px 0px rgba($brandColor, 0.3);
            @media #{$mediaExSmall} {
                width: 100%;
                height: 300px;
            }
            @media #{$mediaSmall} {
                width: 60%;
                height: 400px;
            }
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: rgba(black, .2);
                width: 100%;
                height: 100%;
                z-index: 9;
            } 
        }
        .text-profile {
            margin-left: 0px;
            width: 90%;
            margin: 100px 0;
            @media #{$mediaExSmall} {
                margin: 50px 10px;
            }
            @media #{$mediaSmall} {
                margin: 50px 10px;
            }
            @media #{$mediaMedium} {
                margin: 70px 50px;
            }
            h1 {
                font-size: 50px;
                font-weight: bold;
                color: $mainColor;
                margin: 0;
                text-shadow: 0 0 0 rgba($brandColor, .5);
                @media #{$mediaExSmall} {
                    font-size: 35px
                }
                @media #{$mediaSmall} {
                    font-size: 35px;
                }
                @media #{$mediaMedium} {
                    font-size: 40px
                }
                @media #{$mediaLarge} {
                    font-size: 45px
                }
            }
            p {
                font-size: 21px;
                font-weight: 600;
                color: #f2f2f2;
                letter-spacing: 1px;
                line-height: 28px;
                margin-bottom: 30px;
                margin-top: 10px;
                @media #{$mediaExSmall} {
                    font-size: 17px;
                }
                @media #{$mediaSmall} {
                    font-size: 17px;
                }
                @media #{$mediaMedium} {
                    font-size: 18px;
                }
                @media #{$mediaLarge} {
                    font-size: 19px;
                }
            }
        }
    }
}