/* Navigation Bar */
.navbar {
    position: fixed;
    top: 50%;
    left: 0%;
    transform: translate(0%, -50%);
    background-color: $mainColor;
    padding: 0;
    width: 65px;
    z-index: 999;
    @media #{$mediaExSmall} {
        width: 55px;
        position: absolute;
    }
    @media #{$mediaSmall} {
        width: 55px;
        position: absolute;
    }
    @media #{$mediaMedium} {
        width: 60px;
        position: absolute;
    }
    @media #{$mediaLarge} {
        width: 60px;
    }
    .navbar-nav {
        width: 100%;
        text-align: center;
        .nav-item {
            cursor: pointer;
            padding: 25px 0;
            transition: .2s;
            .nav-link {
                position: relative;
                color: $brandColor;
                padding: 0;
                i {
                    font-size: 25px;
                    @media #{$mediaExSmall} {
                        font-size: 20px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 20px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 22px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 24px;
                    }
                }
                span {
                    text-transform: uppercase;
                    font-weight: 400;
                    font-size: 15px;
                    letter-spacing: 1px;
                    padding: 0 10px;
                    visibility: hidden;
                    opacity: 0;
                    position: absolute;
                    width: 0;
                    height: 40px;
                    background-color: #fff;
                    color: #333;
                    border: 1px solid $primaryColor;
                    text-align: center;
                    line-height: 40px;
                    border-radius: 1px;
                    left: 40px;
                    top: 0;
                    transition: .2s;
                    z-index: -1;
                    @media #{$mediaExSmall} {
                        font-size: 13px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 13px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 14px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 14px;
                    }
                }
            }
            &:hover { 
                span {
                    visibility: visible;
                    opacity: 1;
                    width: auto;
                    left: 80px
                }
            }
        }
    }
}

.active { background-color: #fff }
.active { 
    .nav-link {
        color: #333 !important
    }
}