* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Montserrat', sans-serif
}

// H2
h2 {
    text-transform: uppercase;
    font-weight: 300;
    margin-bottom: 90px;
    position: relative;
    @media #{$mediaExSmall} {
        font-size: 25px;
        margin-bottom: 50px;
    }
    @media #{$mediaSmall} {
        font-size: 27px;
        margin-bottom: 50px;
    }
    @media #{$mediaMedium} {
        font-size: 35px;
        margin-bottom: 70px;
    }
    &::after {
        content: "";
        position: absolute;
        bottom: -10px;
        left: 5px;
        width: 10%;
        height: 2px;
        background-color: $brandColor
    }
}
// H3
h3 {
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 30px;
    letter-spacing: 1px;
    position: relative;
    @media #{$mediaExSmall} {
        font-size: 16px
    }
    @media #{$mediaSmall} {
        font-size: 16px
    }
    @media #{$mediaMedium} {
        font-size: 17px
    }
    &::after {
        content: "";
        position: absolute;
        bottom: -5px;
        left: 0px;
        width: 10%;
        height: 1px;
        background-color: $brandColor
    }
}
/* Placeholder */
::placeholder { color: $brandColor !important }
:-ms-input-placeholder { color: $brandColor !important }
::-ms-input-placeholder { color: $brandColor !important }