/* About Us */
.about-us {
    padding: 100px 30px;
    @media #{$mediaLarge} {
        padding: 100px 70px;
    }
    p {
        color: #333;
        display: inline-block;
        width: 100%;
        font-size: 15px;
        line-height: 25px;
    }
    .box-details {
        padding: 30px;
        box-shadow: 0 30px 120px 0 #dde5ee;
        @media #{$mediaExSmall} {
            padding: 20px;
        }
        @media #{$mediaSmall} {
            padding: 20px;
        }
        @media #{$mediaMedium} {
            padding: 25px;
        }
        @media #{$mediaLarge} {
            padding: 25px;
        }
        .text-details {
            margin-bottom: 10px;
        }
        h4 {
            display: inline-block;
            font-size: 13px;
            margin-right: 10px;
            color: #333
        }
        span {
            font-size: 13px
        }
    }
}

/* Employment */
.employ {
    padding: 100px 30px;
    background-color: #e9eff5;
    @media #{$mediaLarge} {
        padding: 100px 80px;
    }
    .content-timeline {
        border-left: 1px solid #333;
        padding-left: 60px;
        margin-top: 40px;
        @media #{$mediaExSmall} {
            border-left: none;
            padding-left: 0px;
        }
        @media #{$mediaSmall} {
            border-left: none;
            padding-left: 0px;
        }
        @media #{$mediaMedium} {
            border-left: none;
            padding-left: 0px;
        }
        .timeline-box {
            margin-bottom: 40px;
            background-color: #fff;
            box-shadow: 0 0 20px 10px #2e353e0d;
            &:last-child { margin: 0 }
            .experience-info {
                padding: 40px;
                color: $brandColor;
                background-color: $mainColor;
                position: relative;
                &::after {
                    content: "";
                    position: absolute;
                    position: absolute;
                    top: 5px;
                    left: -41px;
                    border-color: transparent #ecc21c transparent transparent;
                    border-width: 20px;
                    border-style: solid;
                    @media #{$mediaExSmall} {
                        left: -38px;
                    }
                    @media #{$mediaSmall} {
                        left: -38px;
                    }
                    @media #{$mediaMedium} {
                        left: -38px;
                    }
                }
                
                &::before {
                    content: "";
                    width: 25px;
                    height: 25px;
                    background-color: #e9eff5;
                    border-radius: 50%;
                    position: absolute;
                    top: 9px;
                    left: -57px;
                    border: 7px solid #2e353e;
                    @media #{$mediaExSmall} {
                        display: none
                    }
                    @media #{$mediaSmall} {
                        display: none
                    }
                    @media #{$mediaMedium} {
                        display: none
                    }
                }
                .info {
                    margin-bottom: 30px;
                    h6 {
                        font-size: 16px;
                        font-weight: 700;
                        margin-bottom: 0;
                    }
                }
                h6 {
                    font-size: 20px;
                    font-weight: 500;
                    margin-bottom: 10px;
                }
                p {
                    margin: 0;
                    font-size: 14px;
                    font-weight: 600;
                    line-height: 30px;
                    span { font-weight: 300; font-size: 16px }
                    a {
                        color: $brandColor;
                        text-decoration: none
                    }
                }
            }
            .experience-description {
                padding: 40px;
                color: $brandColor;
                p {
                    font-size: 15px;
                    line-height: 25px;
                    span {
                        display: block;
                        font-weight: 500;
                        font-size: 16px
                    }
                }
            }
        }
    }
}

/* Education */
.education {
    padding: 100px 30px;
    background-color: #f4f9ff;
    @media #{$mediaLarge} {
        padding: 100px 80px;
    }
    .content-box {
        background-color: #fff;
        box-shadow: 0 0 20px 10px #2e353e0d;;
        padding: 40px;
        @media #{$mediaExSmall} {
            margin-bottom: 30px;
        }
        @media #{$mediaSmall} {
            margin-bottom: 30px;
        }
        @media #{$mediaMedium} {
            margin-bottom: 30px;
        }
        .icon {
            font-size: 25px;
            color: $mainColor;
            @media #{$mediaExSmall} {
                font-size: 20px;
            }
            @media #{$mediaSmall} {
                font-size: 20px;
            }
            @media #{$mediaMedium} {
                font-size: 22px;
            }
            @media #{$mediaLarge} {
                font-size: 24px;
            }
        }
        .text {
            h6 {
                margin-top: 10px;
                font-size: 18px;
                font-weight: 500;
                @media #{$mediaExSmall} {
                    font-size: 16px;
                }
                @media #{$mediaSmall} {
                    font-size: 16px;
                }
                @media #{$mediaMedium} {
                    font-size: 17px;
                }
            }
            p { font-size: 13px;
                @media #{$mediaExSmall} {
                    font-size: 12px;
                }
                @media #{$mediaSmall} {
                    font-size: 12px;
                }
                @media #{$mediaMedium} {
                    font-size: 12px;
                }
            }
            span {
                display: block;
                text-align: right;
                color: $mainColor;
                font-weight: 600
            }
        }
    }
}

/* Skills & Service */
.skills {
    padding: 100px 30px;
    @media #{$mediaLarge} {
        padding: 100px 80px;
    }
    p { margin: 0;
        @media #{$mediaExSmall} {
            font-size: 14px;
        }
        @media #{$mediaSmall} {
            font-size: 14px;
        }
        @media #{$mediaMedium} {
            font-size: 15px;
        }
        @media #{$mediaLarge} {
            font-size: 15px;
        } }
    .progress {
        width: 70%;
        height: 8px;
        border-radius: 20px;
        margin-bottom: 25px;
        .progress-bar {
            background-color: $brandColor;
        }
    }
}

/* Portfilio */
.portfilio {
    padding: 100px 30px;
    background-color: #ecf1f7;
    @media #{$mediaLarge} {
        padding: 100px 80px;
    }
    button {
        font-family: 'Montserrat', sans-serif;
        border: none;
        border-radius: 0;
        padding: 5px 15px;
        font-size: 15px;
        color: #fff;
        letter-spacing: 1px;
        font-weight: 400;
        background-color: $brandColor;
        margin-bottom: 40px;
        @media #{$mediaExSmall} {
            font-size: 14px;
            padding: 5px 10px;
            margin-bottom: 20px;
        }
        @media #{$mediaSmall} {
            font-size: 14px;
            padding: 5px 10px;
            margin-bottom: 20px;
        }
        @media #{$mediaMedium} {
            font-size: 14px;
        }
        &:focus { outline: none }
    }
    .mixitup-control-active {
        background-color: $mainColor
    }
    .box {
        .mix { 
            position: relative;
            overflow: hidden;
            height: 230px;
            .overflow {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba($brandColor, .7);
                text-align: center;
                display: flex;
                justify-content: center;
                align-items: center;
                opacity: 1;
                transition: .3s;
                p {
                    color: #fff;
                    text-transform: uppercase;
                    letter-spacing: 1px;
                    font-size: 15px;
                    margin: 0;
                    &:nth-child(2) {
                        font-size: 13px;
                        color: $mainColor
                    }
                }
            } 
        }
        img { transition: .3s }
        &:hover {
            img { transform: scale(1.1) }
            .overflow { opacity: .09; }
        }
    }
}


/* Conatct Us */
.contact-us {
    overflow: hidden;
    .inline-form {
        padding: 100px;
        background-color: $mainColor;
        @media #{$mediaExSmall} {
            padding: 100px 55px;
        }
        @media #{$mediaSmall} {
            padding: 100px 55px;
        }
        @media #{$mediaMedium} {
            padding: 100px 80px;
        }
        h3 {
            font-size: 20px;
            font-weight: 400;
        }
        .form-group {
            input, textarea {
                font-family: 'Montserrat', sans-serif;
                background-color: transparent;
                border: none;
                border-radius: 0;
                color: $brandColor;
                border-bottom: 1px solid $brandColor;
                &:focus {
                    outline: 0;
                    box-shadow: none
                }
            }
        }
        button {
            font-family: 'Montserrat', sans-serif;
            border: none;
            border-radius: 0;
            padding: 7px 15px;
            font-size: 15px;
            color: #fff;
            letter-spacing: 1px;
            font-weight: 400;
            background-color: $brandColor;
        }
    }
    .contact-info {
        background-image: url('../images/bg-2.jpg');
        background-position: center;
        background-attachment: fixed;
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        padding: 100px;
        @media #{$mediaExSmall} {
            padding: 100px 55px;
        }
        @media #{$mediaSmall} {
            padding: 100px 55px;
        }
        @media #{$mediaMedium} {
            padding: 100px 80px;
        }
        .text {
            color: #fff;
            h3 {
                font-size: 20px;
                font-weight: 400;
                margin-bottom: 70px;
                &::after {
                    background-color: #fff
                }
            }
            p {
                i {
                    font-size: 18px;
                    padding-right: 10px
                }
                span {
                    font-weight: 600;
                    letter-spacing: 1px
                }
            }
        }
    }
}