/* Footer */
.footer {
    padding: 20px;
    .copyright {
        text-align: center;
        p {
            font-size: 14px;
            margin: 0;
        }
    }
}

/* Scroll Top */
.scroll-top {
    position: fixed;
    bottom: 0;
    right: 10px;
    background-color: rgba(#000, .8);
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    z-index: 999;
    cursor: pointer;
    display: none;
    a {
        color: $mainColor;
        font-size: 20px
    }
}